import React from 'react'
import NavbarContent from '../components/navigation/navbar'
import { PageHeadline } from '../components/headline'

/**
 * The BSoD error page, inspired from https://codepen.io/srsgores/pen/kgEdA
 */
const PageNotFound = () => {
  return (
    <React.Fragment>
      <NavbarContent />
      <PageHeadline
        pagename="Error 404"
        pageheadline="Page Not Found"
      ></PageHeadline>
    </React.Fragment>
  )
}

export default PageNotFound
